.photos {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  color: #283645;
}

.photos__title-container {
  width: 100%;
  margin-top: -65px;
  padding-top: 95px;
  padding-bottom: 85px;
  text-align: center;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #283645;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}

.photos__title {
  width: 55%;
  margin: 0 auto;

  @include font-param(24px, 28px);
  font-weight: 300;
  color: #ffffff;
}

.photos__panoram-of-day {
  max-width: 320px;
  align-self: center;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.photos__panoram-label {
  padding: 6px 0;
  width: 140px;
  position: absolute;
  top: 0;
  right: 20px;
  text-align: center;

  background-color: #d22856;
}

.photos__panoram-label p {
  margin: 0;

  @include font-param(12px, 24px);
  text-transform: uppercase;
  font-weight: bold;
  color: #ffffff;
}

.photos__panoram-photo {
  top: 50%;
  left: 50%;
}

.photos__image {
  width: 100%;
}

.photos__wrapper {
  max-width: 280px;
  display: flex;
  flex-direction: column;

  @include wrapper-set;
}

.photos__descriprion {
  width: 90%;
  margin: 0 auto;
  margin-top: 45px;
  margin-bottom: 45px;
  text-align: center;

  @include font-param(14px, 30px);
}

.photos__list {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include list-clear;
  margin-top: 20px;
}

.photos__list .photos__item {
  width: 280px;
  margin-bottom: 18px;
  padding-bottom: 22px;
  background-color: #f2f2f2;
}

.photos__panoram-of-day .photos__item {
  background-color: #f2f2f2;
  padding-bottom: 20px;
}

.photos__text {
  padding: 0 18px;
  margin-top: 0;
  margin-bottom: 10px;

  @include font-param (14px, 24px);
}

.photos__author-info {
  padding: 0 18px;
  margin-top: 15px;

  @include font-param(14px, 24px);
}

.photos__author-name {
  font-weight: bold;
}

.photos__upload-time {
  text-transform: uppercase;
  font-size: 12px;
}

.photos__like {
  position: relative;
  padding-left: 50px;
  margin: 0;

  @include font-param(12px, 24px);
  color: #d22856;
  text-transform: uppercase;
}

.photos__like::before {
  position: absolute;
  content: "";
  width: 13px;
  height: 12px;
  top: 4px;
  right: 250px;

  background-image: url("../img/icon-heart.svg");
}

@media (min-width: $tablet-width) {
  .photos__title-container {
    margin-top: -110px;
    padding-top: 185px;
    padding-bottom: 140px;
    background-image: url("../img/bg-back-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }
  }

  .photos__title {
    @include font-param(50px, 58px);
  }

  .photos__author-info,
  .photos__text {
    padding: 0 25px;
  }

  .photos__author-info {
    margin-top: 20px;
  }

  .photos__panoram-of-day {
    max-width: 660px;
  }

  .photos__panoram-of-day .photos__item {
    position: relative;
    width: 620px;
  }

  .photos__panoram-of-day .photos__like {
    position: absolute;
    top: 20px;
    right: 60px;
  }

  .photos__panoram-of-day .photos__like::before {
    top: 6px;
    right: -30px;
  }

  .photos__panoram-label {
    top: 55px;
    right: 20px;
  }

  .photos__wrapper {
    max-width: 620px;
  }

  .photos__list {
    margin-top: 45px;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
  }

  .photos__item:not(:nth-child(2n)) {
    margin-right: 20px;
  }

  .photos__list .photos__item {
    display: flex;
    flex-direction: column;
    width: 300px;
    min-height: 435px;
  }

  .photos__text-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }

  .photos__descriprion {
    margin-top: 95px;
    margin-bottom: 75px;

    @include font-param(18px, 30px);
  }

  .photos__list .photos__like {
    padding-left: 25px;
    margin: 0;
  }

  .photos__list .photos__like::before {
    left: 255px;
  }
}

@media (min-width: $desktop-width) {
  .photos__title-container {
    padding-top: 180px;
    padding-bottom: 100px;

    background-image: url("../img/bg-back-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }
  }

  .photos__title {
    width: 390px;
  }

  .photos__descriprion {
    width: 600px;
  }

  .photos__wrapper {
    min-width: 960px;
  }

  .photos__list .photos__item {
    min-height: 535px;
    margin-top: 52px;
    margin-bottom: 0;
    margin-right: 0;
  }

  .photos__list .photos__item:not(:nth-child(3n)) {
    margin-right: 20px;
  }

  .photos__panoram-of-day .photos__item--panoram {
    margin: 0 auto;
  }

  .photos__panoram-of-day {
    margin-top: 10px;
    max-width: 100%;
  }

  .photos__panoram-of-day .photos__item {
    width: 940px;
    padding-bottom: 15px;
  }

  .photos__list {
    width: 940px;
    margin: 0 auto;
  }

  .photos__panoram-of-day .photos__author-info,
  .photos__panoram-of-day .photos__text {
    padding-left: 25px;
  }

  .photos__panoram-label {
    top: 55px;
    right: 10px;
  }

  .photos__panoram-of-day .photos__like::before {
    top: 5px;
    right: -35px;
  }

  .photos__panoram-of-day .photos__like {
    top: 23px;
    right: 65px;
  }
}
